/*
Variable declarations
 */
:root {
	--main-bg-primary-color: #26c6da;
	--main-bg-secondary-color: #00acc1;
	--secondary-bg-color: #f2f2f2;
	--main-text-color: #4a4a4a;
	--secondary-text-color: #505050;
}

nav {
	width: 100%;
	box-shadow: 0 2px 0 0 #f5f5f5;
	position: fixed;
	z-index: 1;
	padding: 1rem 2rem;
	box-sizing: border-box;
}

nav .logo {
	width: 100%;
	height: 44px;
	margin: 0 auto;
	background-image: url(/static/media/cogoport_logo.5e01539d.svg);
	background-repeat: no-repeat;
}

* {
	margin: 0;
	padding: 0;
}

html,
body {
	background-color: #fff;
	font-size: 1rem;
	font-family: -apple-system, BlinkMacSystemFont, sans-serif;
	height: 100vh;
	width: 100vw;
}

a {
	text-decoration: none;
}

section {
	padding: 8rem 1.5rem 3rem;
}

.container {
	margin: 0 auto;
	position: relative;
}

.title {
	color: var(--main-text-color);
	font-size: 1.3rem;
	font-weight: 450;
	margin-bottom: 0.5rem;
}

.grid-container {
	display: grid;
    grid-template-columns: repeat(3, 1fr);
	margin-top: 2rem;
	grid-gap: 10px;
}

.selectbox-title {
	font-size: 12px;
    color: rgb(155, 155, 155);
}

.subtitle {
	color: var(--main-text-color);
	font-size: 1.1rem;
}

.input {
	background-size: 18px 18px;
	background-position: 10px 12px;
	background-repeat: no-repeat;
	width: 100%;
	font-size: 0.9rem;
	padding: 12px 16px;
	border: 1px solid #ddd;
	margin-top: 12px;
	max-width: 292px;
	border-radius: 4px;
	background-color: var(--secondary-bg-color);
	outline: none;
	box-sizing: border-box;
}

/* .input.search {
	background-image: url("./icons/search.svg");
} */

select {
	min-height: 38px;
	max-width: 292px;
	width: 100%;
	font-size: 1rem;
	margin-top: 12px;
	padding: 12px 20px 12px 12px;
	-webkit-appearance: none;
	border: 1px solid #ddd;
	background-color: var(--secondary-bg-color);
	color: var(--main-text-color);
	outline: none;
	box-sizing: border-box;
}

.suggestion-div {
	max-width: 292px;
}

.ports-list {
	list-style-type: none;
	font-size: 14px;
	box-shadow: rgba(0, 0, 0, 0.176) 0px 6px 12px;
	max-width: 292px;
	max-height: 365px;
	overflow: scroll;
	border-radius: 4px;
}

.ports-list li {
	padding: 12px 6px;
	cursor: pointer;
}

.ports-list li:hover {
	background-color: rgb(17, 117, 201);
    color: rgb(255, 255, 255);
}
